.phrase {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;

  background-image: url("../images/svitok-hor.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.phrase--box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 70px;
  width: 600px;
  height: 500px;

  text-align: center;
}

.phrase--item {
  margin-top: 30%;
  font-size: 24px;
}

.phrase--autor {
  margin-left: 50%;
  margin-bottom: 10%;
  font-size: 20px;
}
